'use client'

import { useState, useEffect } from 'react'
import Link from 'next/link'
import { useParams } from 'next/navigation'
import { ArrowLeft, Server, BarChart3, CreditCard, Activity } from 'lucide-react'
import { endpointsApi, ApiError } from '@/lib/api-client'

export default function EndpointDetailPage() {
  const params = useParams()
  const id = params.id as string
  const [endpoint, setEndpoint] = useState<any>(null)
  const [stats, setStats] = useState<any>(null)
  const [loading, setLoading] = useState(true)
  const [error, setError] = useState<string | null>(null)

  useEffect(() => {
    loadData()
  }, [id])

  const loadData = async () => {
    try {
      setLoading(true)
      setError(null)
      
      const [endpointRes, statsRes] = await Promise.all([
        endpointsApi.get(id),
        endpointsApi.getStats(id).catch(() => null),
      ])
      
      setEndpoint(endpointRes.endpoint)
      if (statsRes) {
        setStats(statsRes.stats)
      }
    } catch (err) {
      if (err instanceof ApiError) {
        setError(err.message)
      } else {
        setError('Failed to load endpoint')
      }
    } finally {
      setLoading(false)
    }
  }

  if (loading) {
    return (
      <div className="min-h-screen bg-gray-950 text-white flex items-center justify-center">
        <div className="text-center">
          <div className="inline-block animate-spin rounded-full h-8 w-8 border-b-2 border-lime-400"></div>
          <p className="mt-4 text-gray-400">Loading...</p>
        </div>
      </div>
    )
  }

  if (error || !endpoint) {
    return (
      <div className="min-h-screen bg-gray-950 text-white flex items-center justify-center">
        <div className="text-center">
          <p className="text-red-400 mb-4">{error || 'Endpoint not found'}</p>
          <Link href="/dashboard" className="text-lime-400 hover:text-lime-300">
            ‚Üê Back to Dashboard
          </Link>
        </div>
      </div>
    )
  }

  return (
    <div className="min-h-screen bg-gray-950 text-white">
      {/* Navigation */}
      <nav className="sticky top-0 z-50 border-b border-gray-800 bg-gray-950/80 backdrop-blur-xl">
        <div className="max-w-7xl mx-auto px-4">
          <div className="flex items-center justify-between h-16">
            <Link href="/dashboard" className="flex items-center gap-2 text-gray-400 hover:text-white transition-colors">
              <ArrowLeft className="w-4 h-4" />
              Back to Dashboard
            </Link>
          </div>
        </div>
      </nav>

      {/* Content */}
      <div className="max-w-7xl mx-auto px-4 py-8">
        <div className="mb-8">
          <h1 className="text-3xl font-bold mb-2">{endpoint.name}</h1>
          {endpoint.description && (
            <p className="text-gray-400">{endpoint.description}</p>
          )}
        </div>

        {/* Stats Cards */}
        {stats && (
          <div className="grid grid-cols-1 md:grid-cols-4 gap-4 mb-8">
            <div className="bg-gray-900 rounded-lg border border-gray-800 p-6">
              <div className="flex items-center gap-3 mb-2">
                <Activity className="w-5 h-5 text-lime-400" />
                <span className="text-sm text-gray-400">Total Requests</span>
              </div>
              <p className="text-2xl font-bold">{stats.totalRequests || 0}</p>
            </div>
            <div className="bg-gray-900 rounded-lg border border-gray-800 p-6">
              <div className="flex items-center gap-3 mb-2">
                <CreditCard className="w-5 h-5 text-lime-400" />
                <span className="text-sm text-gray-400">Total Payments</span>
              </div>
              <p className="text-2xl font-bold">{stats.totalPayments || 0}</p>
            </div>
            <div className="bg-gray-900 rounded-lg border border-gray-800 p-6">
              <div className="flex items-center gap-3 mb-2">
                <BarChart3 className="w-5 h-5 text-lime-400" />
                <span className="text-sm text-gray-400">Total Revenue</span>
              </div>
              <p className="text-2xl font-bold">
                {stats.totalRevenue ? (parseInt(stats.totalRevenue) / 1e6).toFixed(2) : '0'} USDC
              </p>
            </div>
            <div className="bg-gray-900 rounded-lg border border-gray-800 p-6">
              <div className="flex items-center gap-3 mb-2">
                <Server className="w-5 h-5 text-lime-400" />
                <span className="text-sm text-gray-400">Avg Response Time</span>
              </div>
              <p className="text-2xl font-bold">
                {stats.averageResponseTime ? Math.round(stats.averageResponseTime) : 0}ms
              </p>
            </div>
          </div>
        )}

        {/* Endpoint Details */}
        <div className="bg-gray-900 rounded-lg border border-gray-800 p-6 mb-8">
          <h2 className="text-xl font-bold mb-4">Endpoint Details</h2>
          <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
            <div>
              <span className="text-sm text-gray-500">Endpoint ID</span>
              <p className="text-gray-300 font-mono text-sm mt-1">{endpoint.id}</p>
            </div>
            <div>
              <span className="text-sm text-gray-500">Status</span>
              <p className="text-gray-300 mt-1 capitalize">{endpoint.status}</p>
            </div>
            <div>
              <span className="text-sm text-gray-500">Upstream URL</span>
              <p className="text-gray-300 font-mono text-sm mt-1 break-all">{endpoint.upstreamUrl}</p>
            </div>
            <div>
              <span className="text-sm text-gray-500">Metered URL</span>
              <p className="text-gray-300 font-mono text-sm mt-1">
                {typeof window !== 'undefined' ? window.location.origin : ''}/api/v1/metered/{endpoint.id}
              </p>
            </div>
            <div>
              <span className="text-sm text-gray-500">Price</span>
              <p className="text-gray-300 mt-1">{endpoint.price} {endpoint.tokenSymbol || 'USDC'}</p>
            </div>
            <div>
              <span className="text-sm text-gray-500">Network</span>
              <p className="text-gray-300 mt-1 capitalize">{endpoint.network}</p>
            </div>
            <div>
              <span className="text-sm text-gray-500">Recipient Address</span>
              <p className="text-gray-300 font-mono text-sm mt-1">{endpoint.recipientAddress}</p>
            </div>
            <div>
              <span className="text-sm text-gray-500">Created</span>
              <p className="text-gray-300 mt-1">
                {new Date(endpoint.createdAt).toLocaleString()}
              </p>
            </div>
          </div>
        </div>

        {/* Usage Instructions */}
        <div className="bg-gray-900 rounded-lg border border-gray-800 p-6">
          <h2 className="text-xl font-bold mb-4">How to Use</h2>
          <div className="space-y-4">
            <div>
              <p className="text-sm text-gray-400 mb-2">1. Make a request to your metered endpoint:</p>
              <div className="bg-gray-800 rounded-lg p-4 font-mono text-sm">
                <code className="text-lime-400">
                  curl -X POST {typeof window !== 'undefined' ? window.location.origin : 'https://api.nexflowapp.app'}/api/v1/metered/{endpoint.id} \<br/>
                  &nbsp;&nbsp;-H "Authorization: Bearer YOUR_API_KEY" \<br/>
                  &nbsp;&nbsp;-H "x-payment: YOUR_PAYMENT_HEADER" \<br/>
                  &nbsp;&nbsp;-d '{"{"}"data": "your payload"{"}"}'
                </code>
              </div>
            </div>
            <div>
              <p className="text-sm text-gray-400 mb-2">2. If no payment header is provided, you'll receive a 402 challenge:</p>
              <div className="bg-gray-800 rounded-lg p-4 font-mono text-sm">
                <code className="text-yellow-400">
                  {"{"}<br/>
                  &nbsp;&nbsp;"error": "Payment required",<br/>
                  &nbsp;&nbsp;"challenge": {"{"}<br/>
                  &nbsp;&nbsp;&nbsp;&nbsp;"scheme": "x402",<br/>
                  &nbsp;&nbsp;&nbsp;&nbsp;"amount": "{endpoint.price}",<br/>
                  &nbsp;&nbsp;&nbsp;&nbsp;"recipient": "{endpoint.recipientAddress}"<br/>
                  &nbsp;&nbsp;{"}"}<br/>
                  {"}"}
                </code>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  )
}

